@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,500;0,600;1,500;1,400;1,600&display=swap);
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
}

.attribution {
  font-size: 0.8em;
  text-align: center;
  color: hsl(228, 45%, 44%);
  background-color: rgb(255, 238, 0);
}

p {
  margin: 0;
}

.Card_card__1uqSS {
  padding: 2.3em;
  font-family: "Barlow Semi Condensed", sans-serif;
  border-radius: 1em;
}

.Card_cardPerson__3XBNj {
  display: grid;
  grid-template-columns: 50px minmax(150px, auto);
  grid-template-rows: 2em 2em;
}
.Card_cardPerson__3XBNj img {
  grid-column: 1;
  grid-row: 1/3;
  align-self: center;
  justify-self: start;
  border-radius: 50%;
  border: 3px solid transparent;
  width: 3em;
  height: 3em;
}

.Card_personName__3BWl9 {
  align-self: end;
  padding: 0 0 0 1.3em;
  font-size: 1em;
}
.Card_personStatus__2ScrN {
  align-self: start;
  padding: 0.4em 0 0 1.3em;
  opacity: 0.5;
  font-size: 0.9em;
}

.Card_cardTestimonial__mqHI1 {
  font-size: 1.5em;
  font-weight: 500;
  padding: 1em 0 1em 0;
  line-height: 1.3;
}

.Card_cardStory__2ZJ3u {
  font-size: 1em;
  font-weight: 400;
  opacity: 0.7;
  line-height: 1.4;
}

.Grid_gridMain__qCYvE {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: -webkit-min-content;
  grid-auto-rows: min-content;
  grid-gap: 1.5em;
  gap: 1.5em;
  padding: 3.5em 1.3em;
  background-color: hsl(210, 46%, 95%);
  font-size: 13px;
}

.Grid_gridMain__qCYvE > div:nth-child(1) {
  background-color: hsl(263, 55%, 52%);
  background-image: url(/FM-testimonals-section/static/media/bg-pattern-quotation.6b460e30.svg);
  background-repeat: no-repeat;
  background-position: 85% 0%;
  background-size: 8em 9.5em;
  color: hsl(210, 46%, 95%);
}
.Grid_gridMain__qCYvE > div:nth-child(2) {
  background-color: hsl(217, 19%, 35%);
  color: hsl(210, 46%, 95%);
}

.Grid_gridMain__qCYvE > div:nth-child(3) {
  background-color: hsl(0, 0%, 100%);
}

.Grid_gridMain__qCYvE > div:nth-child(4) {
  background-color: hsl(219, 29%, 14%);
  color: hsl(210, 46%, 95%);
}

.Grid_gridMain__qCYvE > div:nth-child(5) {
  background-color: hsl(0, 0%, 100%);
}

/* Styles for certain tracks of the grid */
.Grid_gridMain__qCYvE > div:nth-child(2n + 1) {
  box-shadow: 20px 20px 50px hsl(217, 19%, 80%);
}

.Grid_gridMain__qCYvE > div:nth-child(1) img {
  border: 3px solid hsl(263, 55%, 72%);
}

.Grid_gridMain__qCYvE > div:nth-child(4) img {
  border: 3px solid hsl(263, 55%, 52%);
}

/* MEDIA */

@media (min-width: 640px) {
  .Grid_gridMain__qCYvE {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1280px) {
  .Grid_gridMain__qCYvE {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 7em;
  }

  .Grid_gridMain__qCYvE > div:nth-child(1) {
    grid-column: 1/3;
    grid-row: 1;
  }
  .Grid_gridMain__qCYvE > div:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
  }

  .Grid_gridMain__qCYvE > div:nth-child(3) {
    grid-column: 1;
    grid-row: 2;
  }

  .Grid_gridMain__qCYvE > div:nth-child(4) {
    grid-column: 2/4;
    grid-row: 2;
  }

  .Grid_gridMain__qCYvE > div:nth-child(5) {
    grid-column: 4;
    grid-row: 1/3;
  }
}

@media (min-width: 1640px) {
  .Grid_gridMain__qCYvE {
    padding: 7em 15em;
    font-size: 18px;
  }
}

